

import DiseaseTypes from "../../../Redux/ActionTypes/DiseaseTypes";

import URLs from "../URLs";

export const getAllDiseases = () => dispatch =>{
    let s_code;

    dispatch({
        type : DiseaseTypes.GET_ALL_DISEASE_API_CALLED,
    })
    fetch(
        `${URLs.BASE_URL}${URLs.disease.get_all_diseases_url}`
    )
    .then(response =>{
        s_code = response.status
        return response.json()
    })
    .then(result =>{
        dispatch({
            type : DiseaseTypes.GET_ALL_DISEASES_DONE,
            payload : {
                data : result.response.diseases
            }
        })
    })
}