
import SVG from "../../../Components/Svg"


const CardSelection = ({ onClick, disabled, ...props }) => {
    return (
        <>
            <div onClick={(e)=>{
                if (!disabled){
                    onClick(e)
                }
            }} className={` group p-6 rounded-3xl flex flex-col gap-3 relative ${props.selected ? 'border-[#24d0d0] border' : 'border-gray-200 border'} ${disabled ? 'cursor-not-allowed grayscale-[1]' : 'cursor-pointer'}`}>
                {props.svg}
                <p className={`${props.selected ? 'text-[#24d0d0] font-bold' : 'text-[#0A1C4B]'} text-center font-[500]`}>{props.title}</p>
                {
                    props.selected &&
                    <div className="absolute flex items-center justify-center w-8 h-8 rounded-full bg-[#24d0d0] right-2 top-2 translate-x-1/2 -translate-y-1/2">
                        <SVG.TickIcon />
                    </div>
                }
            </div>
        </>
    )
}

CardSelection.defaultProps = {
    onClick: () => { }
}

export default CardSelection