

import { useDispatch } from 'react-redux'
import DoctorPreview from '../../../Components/CreateProfile/Preview/DoctorPreview'
import HospitalPreview from '../../../Components/CreateProfile/Preview/HospitalPreview'
import TcForm from '../../../Components/TcForm'

import { CreateProfile } from '../../../Adapters/Apis/Profile'


const CreateProfilePreview = ({ onUpdate, ProfileData }) => {
      const dispatch = useDispatch()
      const handleCreate = () => {
            dispatch(CreateProfile(ProfileData,))
      }

      return (
            <>
                  <div>
                        <TcForm.Button
                              onClick={handleCreate}
                        >
                              Create
                        </TcForm.Button>
                  </div>
                  <div>
                        {
                              ProfileData.user_type == 'Doctor' ?
                                    <DoctorPreview />
                                    :
                                    ProfileData.user_type == "Hospital" ?
                                          <HospitalPreview />
                                          :
                                          <></>
                        }
                  </div>
            </>
      )
}

export default CreateProfilePreview
