


const elementIsObject = (element) =>{
    if (typeof element == 'object'){
        return true
    }
    return false
}

const elementIsFile = (element) =>{
    if (element instanceof File){
        return true
    }
    return false
}

export const getFormData = (data) =>{
    let form_data = new FormData()

    for (let key in data){
        let value = data[key]
        if (!value){
            continue
        }

        console.log(typeof value)
        if (Array.isArray(value)){
            value.forEach(inn_val =>{
                form_data.append(key, inn_val)
            })
        }
        else if (elementIsObject(value) && !elementIsFile(value)){
            form_data.append(key, JSON.stringify(value))
            // addObjectDataToFormData(form_data, value)
        }
        else{
            form_data.append(key, value)
        }
    }


    return form_data
}