


import { useEffect, useState } from "react"
import Svg from "../../../Components/Svg"
import AvailabilityDayColumn from "../../../Components/CreateProfile/Availability/AvailabilityDayColumn"
import TcForm from "../../../Components/TcForm"
import { AddTimeSlotPopup } from "../../../Components/CreateProfile/Availability/TimeSlotBtn"




const ALLOWED_DAYS = [
      'Monday',
      'Tuesday',
      'Wednesday',
      'Thursday',
      'Friday',
      'Saturday',
      'Sunday',
]

const Day = (props) => {
      return (
            <>
                  <div
                        className={`${props.disabled ? 'border-[#878F96] cursor-not-allowed' : props.active ? 'border-[#24D0D0] bg-[#24d0d01a] cursor-pointer' : 'hover:bg-[#24d0d01a] hover:border-[#24D0D0] cursor-pointer'}  relative px-6 py-2 group  border rounded-xl`}
                        onClick={(e) => {
                              if (!props.disabled) {
                                    props.onClick(e)
                              }
                        }}
                  >
                        <p className={`${props.disabled ? 'text-[#676b6f]' : props.active ? 'text-[#24D0D0]' : 'text-[#8B8B8B] group-hover:text-[#24D0D0]'} text-[15px]`}>{props.text} </p>
                        {
                              props.active ?
                                    <div className={`${props.disabled ? 'bg-[#878F96]' : 'bg-[#19D5D5]'} absolute flex items-center justify-center border-[3px] border-[white] w-7 h-7 rounded-full top-[-8px] right-[-10px]`}>
                                          <Svg.TickIcon className='transform scale-75' />
                                    </div>
                                    :
                                    <></>
                        }
                  </div>
            </>

      )
}



const ProfileAvailability = ({ onUpdate, ProfileData }) => {

      const [selectedAvailability, setAvailability] = useState('24_HOURS_OPEN')
      const [is_availability_disabled, setAvailabilityDisabled] = useState(true)
      const [selectedDays, setSelectedDays] = useState([])
      const [slots, setSlots] = useState({})

      const [popup_24_7_price, set24_7Popup] = useState(false)
      // CHOICES ARE 
      // ~ 24_HOURS_OPEN
      // ~ AVAILABILITY_TIME_SLOTS


      const handleUpdate = () => {
            if (selectedAvailability == '24_HOURS_OPEN') {
                  set24_7Popup(true)
            }
            else {
                  onUpdate({ 
                        slots: slots, 
                        active_step: ProfileData.active_step + 1 ,
                        availability : selectedAvailability
                  })
            }
      }

      const handleNewSlotAdded = (data) => {

            let new_slots = {
                  ...slots
            }
            new_slots[data.day] = new_slots[data.day] || []
            new_slots[data.day].push(data)

            setSlots(new_slots)
      }


      useEffect(() => {
            if (selectedAvailability == '24_HOURS_OPEN') {
                  setAvailabilityDisabled(true)
            }
            else {
                  setAvailabilityDisabled(false)
            }

      }, [selectedAvailability])

      return (
            <>
                  {
                        popup_24_7_price &&
                        <AddTimeSlotPopup
                              allowTime={false}
                              onAddSlot={(data)=>{
                                    onUpdate({ 
                                          doctor_fee : data,
                                          active_step: ProfileData.active_step + 1 ,
                                          availability : selectedAvailability
                                    })
                              }}
                              setPopupOpen={set24_7Popup}
                              selected_day='24/7'
                              heading='Add 24/7 Online Availability Fee'
                        />
                  }
                  <div className="flex justify-center items-center">
                        <div className="border border-gray-200 w-full max-w-[1050px] p-4 rounded-xl">
                              <div className="flex flex-col
                               gap-1">
                                    <div className="flex items-center justify-between gap-2">
                                          <h3 className="text-[#6F6F6F] text-2xl font-[500]">Select  Your <span className="text-[#0a1c4b] !font-[600]"> Online </span> Availability Days</h3>
                                          <TcForm.SwitchButton
                                                checked={selectedAvailability == '24_HOURS_OPEN' ? true : false}
                                                options={[
                                                      { label: 'Available 24/7', value: '24_HOURS_OPEN', active_side: true, show: true },
                                                      { label: 'TimeSlots', value: 'AVAILABILITY_TIME_SLOTS', active_side: false, show: false },
                                                ]}
                                                onChange={(e) => {
                                                      setAvailability(e.target.value)
                                                }}
                                          />
                                    </div>
                                    <p className="text-[#6F6F6F] text-[14px]">Your ability will not help if you do not give your availability</p>
                              </div>
                              <div className="mt-10 flex gap-3">
                                    {
                                          ALLOWED_DAYS.map((day, index) => {
                                                return (
                                                      <>
                                                            <Day
                                                                  disabled={is_availability_disabled}
                                                                  key={index}
                                                                  text={day}
                                                                  active={selectedDays.includes(day) ? true : false}
                                                                  onClick={() => {
                                                                        if (selectedDays.includes(day)) {
                                                                              setSelectedDays([...selectedDays.filter(itm => itm != day)])
                                                                        }
                                                                        else {
                                                                              setSelectedDays([...selectedDays, day])
                                                                        }
                                                                  }}
                                                            />
                                                      </>
                                                )
                                          })
                                    }
                              </div>
                              <div className="flex gap-4  mt-4">
                                    {
                                          ALLOWED_DAYS.map((day, index) => {
                                                return (
                                                      <>
                                                            <AvailabilityDayColumn
                                                                  is_selected={selectedDays.includes(day) ? true : false}
                                                                  disabled={is_availability_disabled}
                                                                  key={index}
                                                                  slots={slots[day] || []}
                                                                  text={day}
                                                                  onAddSlot={(data) => {
                                                                        handleNewSlotAdded({
                                                                              ...data,
                                                                              day: day
                                                                        })
                                                                  }}
                                                            />
                                                      </>
                                                )
                                          })
                                    }
                              </div>
                              <TcForm.Button
                                    side='RIGHT'
                                    onClick={handleUpdate}
                                    loading={false}
                                    className='mt-4'
                              >
                                    <span className={`flex items-center gap-2`}>
                                          <span className="text-white">Next</span>
                                          <span><Svg.LongArrow color={'#fff'} direction='RIGHT' /></span>
                                    </span>
                              </TcForm.Button>
                        </div>
                  </div>
            </>
      )
}


ProfileAvailability.defaultProps = {
      onUpdate: () => { },
      ProfileData: {}
}

export default ProfileAvailability