import SVG from "../../../Components/Svg"
import CardSelection from "./CardSelection"


let OFFERED_TYPES = [
      {
            title: 'Doctor',
            svg: <SVG.CreateProfileDoctorSvg />,
            value: 'Doctor',
            count : 1,
      },
      {
            title: 'Hospital/Clinic',
            svg: <SVG.CreateProfileHospitalSvg />,
            value: 'Hospital',
            count : 20,
      },
      {
            title: 'Pharmacy',
            svg: <SVG.CreateProfilePharmacySvg />,
            value: 'Pharmacy',
            count : 20,
      },
      // {
      //       title: 'Lab',
      //       svg: <></>,
      // },
]



const SelectBusinessType = ({ ProfileData, onUpdate, registered_profiles }) => {

      const handleCardSelection = (data) => {
            onUpdate({ user_type: data.selected_card })
      }

      return (
            <>
                  <div className="mt-20">
                        <div className="text-center">
                              <h3 className="text-[#0a1c4b] text-xl font-[500]">Whats your Business type? </h3>
                              <p className="text-[#24d0d0] text-lg font-[500]">Register As</p>
                        </div>
                        <div className="flex items-center flex-wrap gap-8 justify-center mt-5">
                              {
                                    OFFERED_TYPES.map((offr_type, index) => {
                                          return (
                                                <>
                                                      <CardSelection
                                                            {...offr_type}
                                                            // disabled={registered_profiles[offr_type.value.toLowerCase()] >= offr_type.count ? true : false }
                                                            key={index}
                                                            selected={ProfileData?.user_type == offr_type.value ? true : false}
                                                            onClick={() => {
                                                                  handleCardSelection({selected_card : offr_type.value})
                                                            }}
                                                      />
                                                </>
                                          )
                                    })
                              }
                        </div>
                  </div>
            </>
      )
}

SelectBusinessType.defaultProps = {
      ProfileData: {},
      onUpdate: () => { }
}

export default SelectBusinessType
