

import { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import CreateProfile_StepsHeader from "./Header"
import SelectBusinessType from "./Step1"
import GeneralDetails from "./Step2"
import ProfileAvailability from "./Step3"
import ProfileDocument from "./Step4"
import CreateProfilePreview from "./Step5"
import { getMyRegisteredProfiles } from "../../Adapters/Apis/Profile"


const CreateProfilePage = () => {
      const state = useSelector(state => state)
      const dispatch = useDispatch()
      const [ProfileData, setProfileData] = useState({ active_step: 1 })
      const [registered_profiles, setRegiteredProfiles] = useState({})

      const handleData = (data) => {
            let new_values = {}
            for (let dt in data) {
                  new_values[dt] = data[dt]
            }
            setProfileData({
                  ...ProfileData,
                  ...new_values
            })
      }

      useEffect(() => {
            if (state.profile.sidebar_bottom_profile.is_api_called && state.profile.sidebar_bottom_profile.is_data_updated && state.profile.registered_profiles.types.length == 0) {
                  if (state?.profile?.sidebar_bottom_profile?.data?.id) {
                        dispatch(
                              getMyRegisteredProfiles({ user_id: state?.profile?.sidebar_bottom_profile?.data?.id })
                        )
                  }
            }
            else {
                  // need to call api for bottom 
            }
      }, [state?.profile?.sidebar_bottom_profile?.data])

      useEffect(() => {
            setRegiteredProfiles(state.profile.registered_profiles.types)
      }, [state.profile.registered_profiles.types])


      console.log(registered_profiles)
      return (
            <div>
                  <CreateProfile_StepsHeader
                        onUpdate={handleData}
                        ProfileData={ProfileData}
                        onSubmit={(e) => {
                              handleData({ active_step: ProfileData.active_step + 1 })
                        }}
                        onGoBack={(e) => {
                              handleData({ active_step: ProfileData.active_step - 1 })
                        }}
                  />
                  {
                        ProfileData.active_step == 1 &&
                        <SelectBusinessType
                              registered_profiles={registered_profiles}
                              onUpdate={handleData}
                              ProfileData={ProfileData}
                        />
                  }
                  {
                        ProfileData.active_step == 2 &&
                        <GeneralDetails
                              onUpdate={handleData}
                              ProfileData={ProfileData}
                        />
                  }
                  {
                        ProfileData.active_step == 3 &&
                        <ProfileAvailability
                              onUpdate={handleData}
                              ProfileData={ProfileData}
                        />
                  }
                  {
                        ProfileData.active_step == 4 &&
                        <ProfileDocument
                              onUpdate={handleData}
                              ProfileData={ProfileData}
                        />
                  }
                  {
                        ProfileData.active_step == 5 &&
                        <CreateProfilePreview
                              onUpdate={handleData}
                              ProfileData={ProfileData}
                        />
                  }
            </div>
      )
}

export default CreateProfilePage