

import DayTimeSlot from './TimeSlotCard'
import TimeSlotBtn from './TimeSlotBtn'
import { useEffect, useState } from 'react'

import { TWENTY_MINUTES_DURATION } from '../../../Constants/Data/Time'


const AvailabilityDayColumn = ({ disabled, onAddSlot, text, slots, is_selected, ...props }) => {
    const [taken_slots, setTakenSlots] = useState([])

    return (
        <>
            <div className="flex flex-col gap-3">
                <p className={`${is_selected ? 'text-[#484848]' : 'text-[#b0b0b0]'} text-xl font-[400]`}>{text}</p>
                {
                    slots?.map((slot, index) => {
                        return (
                            <>
                                <DayTimeSlot
                                    disabled={disabled}
                                    is_selected={is_selected}
                                    key={index}
                                    slot={slot}
                                />
                            </>
                        )
                    })
                }
                <TimeSlotBtn
                    disabled={disabled ? true : is_selected ? false : true}
                    selected_day={text}
                    onAddSlot={onAddSlot}
                    slots={slots}
                />
            </div>
        </>
    )
}

AvailabilityDayColumn.defaultProps = {
    disabled: false,
    text: 'Please provide text props',
    slots: [],
    is_selected: false,
    onAddSlot : (data) =>{}
}

export default AvailabilityDayColumn