import React from 'react'
import { Link } from 'react-router-dom'

const Navitem = ({ path, showText, ...props }) => {
      return (
            <Link to={path ? path : '#'}>
                  <div className={`flex mb-1 items-center gap-2 py-2 px-2 mx-2 rounded-lg flex-1 cursor-pointer relative ${props.active ? 'bg-[#24d0d01a]' : 'hover:bg-gray-100'}`}>
                        <span className='w-[40px] flex items-center justify-center'>
                              {props.svg}
                        </span>
                        {
                              showText &&
                              <p className={`${props.active ? 'text-[#24D0D0] font-medium' : ''}  text-[14px]`}>{props.name}</p>
                        }
                        {
                              props.active &&
                                    <div className="absolute h-full w-1 rounded-l-lg bg-[#24D0D0] top-0 right-0 translate-x-2">
                                    </div>
                        }
                  </div>
            </Link>
      )
}

Navitem.defaultProps = {
      path: '/',
      svg: <></>,
      active: false,
      name: 'Navitem',
      showText: true
}


export default Navitem