import TableRow from "./TableRow"



const Table = ({ rows }) => {
    return (
        <>
            <div>
                <TableRow />
                {
                    rows.map(
                        (single_row, index) => {
                            return (
                                <TableRow columns={single_row.columns} />
                            )
                        }
                    )
                }
            </div>
        </>
    )
}

Table.defaultProps = {
    rows: []
}

export default Table