import DiseaseTypes from "../ActionTypes/DiseaseTypes"


const initialState = {
    diseases : {
        data : [],
        is_api_called : false,
        is_data_updated : false,
    }
}

const DiseaseReducer = (state = initialState, action) =>{

    let {type, payload} = action
    switch (type){
        case DiseaseTypes.GET_ALL_DISEASE_API_CALLED:
            return {
                ...state,
                diseases : {
                    ...state.diseases,
                    is_api_called : true
                }
            }
        case DiseaseTypes.GET_ALL_DISEASES_DONE:
            return {
                ...state,
                diseases : {
                    ...state.diseases,
                    data : payload.data,
                    is_data_updated : true
                }
            }
        default :
            return state
    }
}

export default DiseaseReducer