

import specialityTypes from "../../../Redux/ActionTypes/specialityTypes";

import URLs from "../URLs";

export const getAllSpecialities = () => dispatch =>{
    let s_code;

    dispatch({
        type : specialityTypes.GET_ALL_SPECIALITITES_API_CALLED,
    })
    fetch(
        `${URLs.BASE_URL}${URLs.speciality.get_all_specialities_url}`
    )
    .then(response =>{
        s_code = response.status
        return response.json()
    })
    .then(result =>{
        dispatch({
            type : specialityTypes.GET_ALL_SPECIALITITES_DONE,
            payload : {
                data : result.response.specialities
            }
        })
    })
}