import ProfileTypes from "../ActionTypes/ProfileTypes"



const initialState = {
    registered_profiles : {
        types : []
    },
    sidebar: {
        data: {
            active_profile: undefined,
            profiles: []
        },
        is_api_called: false,
        is_data_updated: false,
    },
    sidebar_bottom_profile : {
        data: {},
        is_api_called: false,
        is_data_updated: false,
    },
    dashboard_profile : {
        data: {},
        is_api_called: false,
        is_data_updated: false,
    }
}

const ProfileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ProfileTypes.GET_MY_REGISTERED_PROFILES:
            return {
                ...state,
                registered_profiles : {
                    ...state.registered_profiles,
                    types : action.payload.types
                }
            }
        case ProfileTypes.GET_MY_DASHBOARD_ACTIVE_PROFILE:
            return {
                ...state,
                dashboard_profile: {
                    ...state.dashboard_profile,
                    data: action.payload.data,
                    is_data_updated: true
                }
            }
        case ProfileTypes.GET_MY_DASHBOARD_ACTIVE_PROFILE_API_CALLED:
            return {
                ...state,
                dashboard_profile: {
                    ...state.dashboard_profile,
                    is_api_called: action.payload
                }
            }
        case ProfileTypes.GET_MY_SIDEBAR_BOTTOM_ACTIVE_PROFILE:
            return {
                ...state,
                sidebar_bottom_profile: {
                    ...state.sidebar_bottom_profile,
                    data: action.payload.data,
                    is_data_updated: true
                }
            }
        case ProfileTypes.GET_MY_SIDEBAR_BOTTOM_ACTIVE_PROFILE_API_CALLED:
            return {
                ...state,
                sidebar_bottom_profile: {
                    ...state.sidebar_bottom_profile,
                    is_api_called: action.payload
                }
            }
        case ProfileTypes.GET_MY_SIDEBAR_PROFILES_DONE:
            let new_data = action.payload.data

            let active_profile = new_data.find(itm => itm.is_selected)
            let other_profiles = new_data.filter(itm => !itm.is_selected)

            return {
                ...state,
                sidebar: {
                    ...state.sidebar,
                    data: {
                        active_profile: active_profile,
                        profiles: other_profiles
                    },
                    is_data_updated: true
                }
            }

        case ProfileTypes.GET_MY_PROFILES_API_CALLED:
            return {
                ...state,
                sidebar: {
                    ...state.sidebar,
                    is_api_called: action.payload
                }
            }
        default:
            return state
    }
}

export default ProfileReducer