
import { useEffect, useState } from "react"
import Dropdown from "./Dropdown"
import InputField from "./InputField"


const PhoneNumber = ({
    type,
    label,
    required,
    placeholder,
    name,
    value,
    error,
    onChange,
    showTitle,
    id
}) => {

    const [code, setCode] = useState('92')
    const [mobile_number, setMobileNumber] = useState('')

    const handleOnChange = (inp_value) => {
        onChange({ target: { name: name, value: inp_value } })
    }

    // useEffect(() =>{
    //     if (code || mobile_number){
    //         let phone_number = `+${code}${mobile_number ? `-${mobile_number}` : ''}`
    //         onChange({target : { name : name, value : phone_number}})
    //     }
    // } , [code, mobile_number])

    useEffect(() => {
        if (value) {
            let [v_code, v_number] = value.split('-')
            if (v_code) {
                v_code = v_code.replace('+', '')
                setCode(v_code)
            }
            if (v_number) {
                setMobileNumber(v_number)
            }
        }
    }, [value])



    return (
        <>
            <div>
                {
                    showTitle &&
                    <label htmlFor={id ? id : name} className="text-[#6d8cba] mb-[6px] block text-[14px]">{label} {required ? '*' : ''}</label>
                }
                <div className={`flex items-start gap-2 justify-between`}>
                    <Dropdown
                        showTitle={false}
                        className={`max-w-[100px]`}
                        placeholder={'+000'}
                        allowSearch={false}
                        value={code}
                        disabled
                        options={[
                            { label: '+92', value: '92' },
                            { label: '+971', value: '971' },
                        ]}
                        onChange={(e) => {
                            handleOnChange(
                                `+${e.target.value}-${mobile_number}`
                            )
                        }}
                    />
                    <div className={`flex-1`}>
                        <InputField
                            required={required}
                            type={'number'}
                            showTitle={false}
                            value={mobile_number}
                            placeholder={'Mobile Number'}
                            onChange={(e) => {
                                handleOnChange(
                                    `+${code}-${e.target.value}`
                                )
                            }}
                        />
                    </div>
                </div>
            </div>
        </>
    )
}

PhoneNumber.defaultProps = {
    showTitle: true,
    label: 'Please provide Label',
    required: false,
    placeholder: 'Please Provide Placeholder',
    name: 'Please provide Name',
    value: undefined,
    error: '',
    onChange: () => { },
    id: 'please provide id'
}

export default PhoneNumber