import { useEffect, useState } from "react"
import Svg from "../Svg"


const Dropdown = ({ onChange, type, disabled, showTitle, options, value, name, label, onBlur, id, required, placeholder, error, className, allowSearch }) => {

    const [is_error, setIsError] = useState(false)
    const [is_open, setIsOpen] = useState(false)
    const [optData, setOptionsData] = useState([])
    const [searchText, setSearchText] = useState('')
    const [selected_value, setSelectedValue] = useState(undefined)

    const handleOnBlur = (event) => {
        if (required) {
            if (!selected_value) {
                setIsError('This field is required')
            }
        }
        onBlur({})
    }

    const handleChange = (option) => {
        setIsError(false)
        setIsOpen(false)
        setSearchText('')
        onChange({ target: { name, value: option.value } })
    }

    useEffect(() => {
        setIsError(error)
    }, [error])

    useEffect(() => {
        let data = options.filter(itm => !itm.disabled)
        data = options.filter(itm => `${itm?.label}`?.toLowerCase().includes(searchText.toLowerCase()) || itm?.value?.toLowerCase().includes(searchText.toLowerCase()))
        setOptionsData(data)
    }, [options, searchText])

    useEffect(() => {
        let sel_val = options.find(itm => itm.value == value)
        if (sel_val) {
            setSelectedValue(sel_val)
        }
        else {
            setSelectedValue(undefined)
        }
    }, [options, value])

    return (
        <>
            <div className={`mb-[10px] flex-1 ${className ? className : ''}`}>
                {
                    showTitle &&
                    <label htmlFor={id ? id : name} className="text-[#6d8cba] mb-[6px] block text-[14px]">{label} {required ? '*' : ''}</label>
                }
                {/* <input
                    type={type}
                    value={value}
                    autocomplete="off"
                    name={name}
                    id={id ? id : name}
                    className={`block w-full bg-[#23d0d010] rounded-[10px] py-[16px] px-[20px] outline-none text-[15px] text-[#6c747e] border-[1.4px] border-[#23d0d010] ${is_error ? 'text-[#d64139] border-[#f25b52] bg-[#f25a5220]' : ''}`}
                    placeholder={placeholder}
                    onChange={handleChange}
                    onBlur={handleOnBlur}
                /> */}

                <div className={`relative ${disabled ? 'cursor-not-allowed' : ''}`}>
                    <div
                        className={`flex ${disabled ? 'cursor-not-allowed' : 'cursor-pointer'} items-center justify-between w-full bg-[#23d0d010] rounded-[10px] py-[16px] px-[20px] outline-none text-[15px] text-[#6c747e] border-[1.4px] border-[#23d0d010] ${is_error ? 'text-[#d64139] border-[#f25b52] bg-[#f25a5220]' : ''}`}
                        onClick={(e) => {
                            if (!disabled) {
                                let is_already_open = is_open
                                setIsOpen(!is_open)
                                if (is_already_open) {
                                    handleOnBlur()
                                }
                            }
                        }}
                    >
                        <div>
                            {
                                selected_value ?
                                    <span>
                                        {selected_value.label}
                                    </span>
                                    :
                                    <span className={`text-[#9ab2d2]`}>
                                        {placeholder}
                                    </span>
                            }
                        </div>
                        <span>
                            <Svg.Chevron color={`${is_error ? '#f25b52' : ''}`} className={`transition-all`} direction={is_open ? '' : `BOTTOM`} />
                        </span>
                    </div>
                    {
                        (is_open) ?
                            <div className={`p-2 rounded-lg bg-white border border-gray-200 left-0 right-0 absolute top-full max-h-[400px] overflow-y-auto translate-y-2`}>
                                {
                                    allowSearch &&
                                    <div className="flex items-center border border-[#e6ecf6] mb-[10px] py-[7px] px-[10px] rounded-[8px]">
                                        <span>
                                            <Svg.SearchIcon />
                                        </span>
                                        <input
                                            type="text"
                                            className={`flex-1 placeholder:text-[#9ab2d2] w-full ml-[10px] outline-none border-0 text-[14px]`}
                                            placeholder="Search..."
                                            onChange={(e) => {
                                                setSearchText(e.target.value)
                                            }}
                                        />
                                    </div>
                                }
                                <div>
                                    {
                                        optData?.length > 0 ?
                                            optData?.map((option, index) => {
                                                return (
                                                    <>
                                                        <div
                                                            key={index}
                                                            className={`text-gray-600 text-[16px] px-2 py-1 rounded-lg ${option.disabled ? 'cursor-not-allowed' : 'hover:bg-[#23d0d010] cursor-pointer'} ${option.border ? 'border border-gray-200' : ''} ${options.length != (index + 1) ? 'mb-1' : ''}`}
                                                            onClick={() => {
                                                                if (!option.disabled) {
                                                                    handleChange(option)
                                                                }
                                                            }}
                                                        >
                                                            {option?.label}
                                                        </div>
                                                    </>
                                                )
                                            })
                                            :
                                            <>
                                                <div className="flex rounded-[10px] items-center border rounded-10px py-2 px-2">
                                                    <span>
                                                        <Svg.NotFoundSearchItem />
                                                    </span>
                                                    <p className="ml-2 flex-1 text-[#9ab2d2] text-[15px] line-clamp-1">No Record Found</p>
                                                </div>
                                            </>
                                    }
                                </div>

                            </div>
                            :
                            <></>
                    }
                </div>
                {is_error &&
                    <span className={`text-[#d64139] text-[12px] max-w-max ml-auto line-clamp-1 block`}>{is_error}</span>
                }
            </div>
        </>
    )
}


Dropdown.defaultProps = {
    onChange: () => { },
    onBlur: () => { },
    type: 'text',
    showTitle: true,
    label: 'Enter Title Props',
    id: undefined,
    required: false,
    placeholder: 'Please provide placeholder',
    name: 'asdfas',
    value: '',
    error: false,
    options: [],
    className: '',
    allowSearch: true,
    disabled: false
}
export default Dropdown