import { useEffect, useState } from "react"
import Svg from "../../../Components/Svg"

import { connect } from 'react-redux'
import TcForm from "../../../Components/TcForm"




const SelectionCard = ({ data, onClick, selected }) => {
    return (
        <>
            <div
                className={`border py-[6px] transition-all rounded-full h-max group ${selected ? 'bg-[#24D0D0] border-[#24D0D0] gap-2 flex items-center px-3' : 'hover:bg-[#24D0D010] px-5 hover:border-[#24D0D0]'} cursor-pointer`}
                onClick={onClick}
            >
                {
                    selected &&
                    <Svg.TickIcon />
                }
                <p className={`text-center ${selected ? 'text-white' : 'text-[#9B9B9B] group-hover:text-[#747474]'} `}>{data.name}</p>
            </div>
        </>
    )
}

SelectionCard.defaultProps = {
    data: {},
    onClick: () => { },
    selected: false,
}

const SelectionSection = ({ content, heading, description, ProfileData, onUpdate, onGoBack, setSelectedData, selectedData, ...props }) => {
    const [selectionData, setSelectionData] = useState({})

    const [search_text, setSearchText] = useState('')

    const handleSelect = (sel_data) => {
        let id = sel_data.id
        let new_ids = [...selectedData]
        if (new_ids.includes(id)) {
            let id_index = new_ids.findIndex(itm => itm == id)
            if (id_index >= 0) {
                new_ids.splice(id_index, 1)
            }
        }
        else {
            new_ids.push(id)
        }
        setSelectedData(new_ids)
    }

    useEffect(() => {
        if (content) {
            let new_content = {...content}
            if (search_text){
                new_content.data = new_content.data?.filter(itm => itm?.name?.toLowerCase()?.includes(search_text?.toLowerCase()))
            }
            setSelectionData(new_content)
        }
    }, [content, search_text])

    return (
        <>
            <div className="mt-8">
                <div className="flex items-center mb-2">
                    {
                        onGoBack &&
                        <span
                            className={`cursor-pointer bg-gray-50 rounded-md px-4 py-2 border`}
                            onClick={onGoBack}
                        >
                            <Svg.LongArrow />
                        </span>
                    }
                    <h3 className="text-[#0A1C4B] ml-2 text-2xl font-[600]">{heading} <span className="text-[12px] text-gray-700"><span className="text-[#8231b3]">(</span>{selectedData.length} Selected<span className="text-[#8231b3]">)</span></span></h3>
                </div>
                <p className="text-[#a1a1a1] text-[14px] font-[400]">{description}</p>
            </div>
            {
                content.is_data_updated ?
                    content?.data?.length > 0 ?
                        <>
                            <div className="mt-4">
                                <TcForm.InputField
                                    value={search_text}
                                    showTitle={false}
                                    placeholder={'Search...'}
                                    onChange={(e) => {
                                        setSearchText(e.target.value)
                                    }}
                                />
                            </div>
                            <div className="flex gap-2 flex-wrap mt-4">
                                {
                                    selectionData?.data?.map((sel_data, index) => {
                                        return (
                                            <>
                                                <SelectionCard
                                                    key={index}
                                                    data={sel_data}
                                                    selected={selectedData.includes(sel_data.id)}
                                                    onClick={(e) => {
                                                        handleSelect(sel_data)
                                                    }}
                                                />
                                            </>
                                        )
                                    })
                                }
                            </div>
                        </>
                        :
                        <div className="flex justify-center py-5">
                            not_found
                        </div>
                    :
                    <>
                        <div className="flex justify-center py-10">
                            <Svg.TcLoader />
                        </div>
                    </>
            }
        </>
    )
}


SelectionSection.defaultProps = {
    ProfileData: {},
    onUpdate: () => { },
    onGoBack: () => { },
}

const mapStateToProps = (state) => {
    return {

    }
}

const mapDispatchToProps = {
}

export default connect(mapStateToProps, mapDispatchToProps)(SelectionSection)