import Svg from "../Svg"


const PopupHeader = ({ heading, onCloseClick }) => {
  return (
    <>
      <div className={`px-6 py-4 flex items-center justify-between border-b border-b-gray-200 mb-2`}>
        <span className="text-xl font-medium">
          {heading}
        </span>
        <span className="cursor-pointer" onClick={onCloseClick}>
          <Svg.CircleCrossIcon />
        </span>
      </div>
    </>
  )
}




const Popup = ({ is_open, heading, widthClass, children, onClose, ...props }) => {

  return (
    <>
      <div className={`fixed top-0 right-0 bottom-0 left-0 z-[300] flex items-center justify-center`}>
        <div className={`bg-[#0000005f] backdrop-blur-[4px] absolute top-0 right-0 left-0 bottom-0 z-1`}></div>
        <div className={`z-[2] relative bg-white ${widthClass} rounded-2xl w-full`} >
          <PopupHeader
            heading={heading}
            onCloseClick={()=>{
              onClose()
            }}
          />
          {
            children
          }
        </div>
      </div>
    </>
  )
}

Popup.defaultProps = {
  is_open: true,
  heading: 'Please provide Popup Heading',
  widthClass: 'max-w-[700px]',
  onClose: () => { }
}

export default Popup
