import { useEffect, useState } from "react"
import Svg from "../../../Components/Svg"


const HeaderStep = ({ step, is_active, onClick, is_last }) => {
    return (
        <>
            <div
                className={`flex items-center gap-2 pr-3 relative ${is_active ? 'cursor-pointer' : 'cursor-not-allowed'}`}
                onClick={(e) => {
                    if (is_active) {
                        onClick && onClick(e)
                    }
                }}
            >
                <div className={`h-8 w-8 ${is_active ? 'bg-[#24D0D0]' : 'bg-gray-300'} rounded-full  flex items-center justify-center`}>
                    <p className="text-white">{step?.number}</p>
                </div>
                <p className={`${is_active ? 'text-[#24D0D0]' : 'text-gray-500'} flex-1 text-[14px] whitespace-nowrap`}>{step?.name}</p>
                {
                    !is_last &&
                    <>
                        <span className="absolute translate-x-1 right-0 w-[1px] bottom-[50%] h-[50px] bg-gray-200 origin-bottom -rotate-[32deg]"></span>
                        <span className="absolute translate-x-1 right-0 top-[50%] w-[1px] h-[50px] bg-gray-200 origin-top rotate-[32deg]"></span>
                    </>
                }
            </div>
        </>
    )
}


const CreateProfile_StepsHeader = ({ ProfileData, onUpdate, onSubmit, onGoBack }) => {

    const [prev_ste, setPrevStep] = useState(undefined)
    const [next_step, setNextStep] = useState(undefined)

    const STEPS = [
        {
            name: 'Business Type',
            number: 1,
        },
        {
            name: 'Details',
            number: 2,
        },
        {
            name: 'Availability',
            number: 3,
        },
        {
            name: 'Documents',
            number: 4,
        },
        {
            name: 'Preview',
            number: 5,
        },
    ]

    useEffect(() => {
        setPrevStep(STEPS.find(step => step.number == (ProfileData?.active_step - 1)))
        setNextStep(STEPS.find(step => step.number == (ProfileData?.active_step + 1)))
    }, [ProfileData.active_step])


    return (
        <>
            <div className="flex justify-center items-center p-6">
                <div className="flex items-center gap-4 relative">
                    {
                        prev_ste &&
                        <div
                            className="flex items-center gap-2 cursor-pointer absolute -left-3 -translate-x-full"
                            onClick={(e) => {
                                onGoBack && onGoBack(e)
                            }}
                        >
                            <Svg.LongArrow direction='LEFT' />
                            <p className="text-[#0A1C4B] text-[14px] whitespace-nowrap">{prev_ste?.name}</p>
                        </div>
                    }
                    <div className="flex items-center gap-3 border border-gray-200 rounded-full px-2 py-[6px] overflow-hidden">
                        {
                            STEPS.map((step, index) => {
                                return (
                                    <>
                                        <HeaderStep
                                            key={index}
                                            step={step}
                                            is_active={ProfileData?.active_step >= step.number ? true : false}
                                            is_last={(index + 1) == STEPS.length ? true : false}
                                            onClick={(e) => {
                                                onUpdate({ active_step: step.number })
                                            }}
                                        />
                                    </>
                                )
                            })
                        }
                    </div>
                    {
                        next_step &&
                        <div
                            className="flex items-center gap-2 cursor-pointer absolute -right-3 translate-x-full"
                            onClick={(e) => {
                                onSubmit && onSubmit(e)
                            }}
                        >
                            <p className="text-[#24D0D0] text-[14px] whitespace-nowrap">{next_step?.name}</p>
                            <Svg.LongArrow direction='RIGHT' color='#24D0D0' />
                        </div>
                    }
                </div>
            </div>
        </>
    )
}

export default CreateProfile_StepsHeader