

import { Route, Routes } from "react-router-dom"

export const generateRouting = (required_routes) =>{
	if (!required_routes){
		return []
	}
	
	
    let output_routes = []
	
    required_routes.forEach(route_ =>{
      let these_routings = []
      if (route_.routes){
          these_routings = generateRouting(route_.routes)
      }
      let this_route = <Route path={route_.path} element={route_.element} exact={route_.exact}>
                          {these_routings.map(itm => itm)}
                        </Route>
      output_routes.push(this_route)
    })
    return output_routes
}
