import Svg from "../Svg"



const Button = ({ children, side, color, onClick, loading, className }) => {
    return (
        <>
            <div className={`flex items-center mt-2 ${side == 'LEFT' ? 'justify-start' : side == 'RIGHT' ? 'justify-end' : 'justify-center'} ${className}`}>
                <div
                    className={`px-4 py-2 text-[15px] rounded-md cursor-pointer text-white ${color == 'DARK' ? 'bg-[#0A1C4B]' : 'bg-[#24D0D0]'}`}
                    onClick={(e) => !loading && onClick(e)}
                >
                    {
                        loading ?
                            <>
                                <Svg.TcBtnLoader />
                            </>
                            :
                            children ?
                                children
                                :
                                <></>
                    }
                </div>
            </div>
        </>
    )
}


Button.defaultProps = {
    children: <>Enter Button Text</>,
    color: 'DARK',
    side: 'LEFT',
    onClick: () => { },
    loading: false,
    className: ''
}

export default Button