import React from "react"
import { Outlet, Route, Routes } from "react-router-dom"
import DOCTOR_ROUTES from "./DoctorRoutes"
import PATIENT_ROUTES from "./PatientRoutes"


import Constants from "../Constants"
import { APPLICATION_PATH } from "../Adapters/Apis/URLs"


let TC_ROUTES = {
    'PATIENT': PATIENT_ROUTES,
    'DOCTOR' : DOCTOR_ROUTES
}

class RootRoutes extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            user_routes : [],
            user_type : 'PATIENT'
        }
    }

    componentWillMount(){
        let user_type = localStorage.getItem('user_type')
        let active_user_routes = undefined
        if (!user_type){
            user_type = 'PATIENT'
            localStorage.setItem('user_type', user_type)
        }

        active_user_routes = TC_ROUTES[user_type]
        if (active_user_routes){

            let generated_routes = Constants.functions.generateRouting(active_user_routes)
            this.setState({
                user_routes : generated_routes, 
                user_type : user_type
            })
        }
    }
    
    render(){
        console.log(APPLICATION_PATH)
        return (
            <>
                <Routes>
                    <Route path={`${APPLICATION_PATH}`} element={<Outlet />} >
                        {this.state.user_routes}
                        {/* {
                            this.state.user_routes?.map((u_route, index) =>{
                                return (
                                    <Route key={index} {...u_route} />
                                )
                            })
                        } */}
                    </Route>
                    <Route path="*" element={<>page Not found</>} />

                </Routes>
            </>
        )
    }
}

export default RootRoutes