import { useEffect } from "react"
import { getMyDashboardActiveProfile, getMySidebarBottomActiveProfile } from "../../Adapters/Apis/Profile"
import { useDispatch, useSelector } from "react-redux"




const UserWrapper = ({ children }) => {
    const dispatch = useDispatch()
    const state = useSelector(state => state)

    useEffect(() => {
        if (!state.profile?.sidebar_bottom_profile.is_api_called){
            dispatch(getMySidebarBottomActiveProfile())
        }
    }, [])

    return (
        <>
            {children}
        </>
    )
}

export default UserWrapper