

import { useEffect, useState } from "react"
import Svg from "../Svg"
import { useDispatch, useSelector } from "react-redux"
import { SwitchUserProfile, getMySidebarProfiles } from "../../Adapters/Apis/Profile"
import { useNavigate } from "react-router-dom"


const ProfileDisplay = ({ className, data, onClick, isBottomMain, sideBarActive }) => {
    return (
        <>
            <div
                className={`flex gap-2 items-center transition-all group ${className ? className : ''} ${onClick ? 'cursor-pointer hover:bg-[#0A1C4B] hover:text-white rounded-lg px-[6px] py-[4px]' : ''}`}
                onClick={(e) => {
                    onClick && onClick(e)
                }}
            >
                <div className="w-[32px] h-[32px] bg-cover bg-center bg-no-repeat rounded-md bg-gray-200" style={{ backgroundImage: `url("https://image.shutterstock.com/mosaic_250/2780032/1194497251/stock-photo-portrait-of-smiling-red-haired-millennial-man-looking-at-camera-sitting-in-caf-or-coffeeshop-1194497251.jpg")` }}>
                </div>
                {
                    (!isBottomMain || (sideBarActive)) &&
                    <div>
                        <h3 className={`text-[15px] font-semibold capitalize line-clamp-1 ${onClick ? 'group-hover:text-white' : 'text-[#0A1C4B] '}`}>{data.full_name}</h3>
                        <p className={`text-[12px] line-clamp-1 -mt-1 ${onClick ? 'group-hover:text-gray-200' : 'text-gray-700'}`}>{data?.profile_label}</p>
                    </div>
                }
            </div>
        </>
    )
}

const ProfileOption = ({ icon, name }) => {
    return (
        <>
            <div className="flex mb-1 items-center gap-2 cursor-pointer px-[6px] py-[4px] hover:bg-slate-300 rounded">
                <span>{icon}</span>
                <span className="text-[14px]">{name}</span>
            </div>
        </>
    )
}

const SidebarBottomUser = ({ sideBarActive }) => {

    const state = useSelector(state => state)
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const [is_dropdown_open, setDropDown] = useState(false)
    const [profiles, setProfiles] = useState(undefined)
    const [bottom_active_profile, setBottomActiveProfile] = useState(null)


    const handleDropdownOpen = () => {
        setDropDown(!is_dropdown_open)

        if (!state.profile?.sidebar?.is_api_called) {
            dispatch(
                getMySidebarProfiles()
            )
        }
    }

    const handleProfileSelection = (profile) => {
        localStorage.setItem('user_type', profile.profile_type.toUpperCase())
        dispatch(
            SwitchUserProfile(
                {profile_id : profile.id},
                ()=>{
                    navigate(0)
                }
            )
        )
    }

    useEffect(() => {
        setProfiles(state.profile?.sidebar?.data)
    }, [state.profile?.sidebar?.data])

    console.log(bottom_active_profile)

    useEffect(() =>{
        setBottomActiveProfile(state.profile?.sidebar_bottom_profile)
    } , [state.profile?.sidebar_bottom_profile?.data])


    return (
        <>
            <div className="relative px-3">
                {
                    is_dropdown_open &&
                    <>
                        <div className={`absolute p-2 -translate-y-2 border border-gray-200 transition-all bg-[#ffffffad] backdrop-blur-[10px] rounded-md w-[240px] translate-x-[5px] ${sideBarActive ? 'left-0 bottom-full' : 'bottom-0 left-full translate-x-2'} `}>
                            {
                                profiles && profiles.active_profile ?
                                    <>
                                        <ProfileDisplay
                                            data={profiles.active_profile}
                                        />
                                        <hr className="my-2" />
                                        {
                                            profiles?.profiles?.map((profile, index) => {
                                                return (
                                                    <>
                                                        <ProfileDisplay
                                                            key={index}
                                                            data={profile}
                                                            className={'mb-1'}
                                                            onClick={() => {
                                                                handleProfileSelection(profile)
                                                            }}
                                                        />
                                                    </>
                                                )
                                            })
                                        }
                                        {/*                                         
                                        <ProfileDisplay
                                            data={{ name: "Doctor's Hospital", profile_type: 'Hospital Profile', image: '' }}
                                            className={'mb-1'}
                                            onClick={() => {

                                            }}
                                        />
                                        <ProfileDisplay
                                            data={{ name: "Hameed Lateef Hospital", profile_type: 'Hospital Profile', image: '' }}
                                            className={'mb-1'}
                                            onClick={() => {

                                            }}
                                        />
                                        <ProfileDisplay
                                            data={{ name: "gillon Pharmacy", profile_type: 'Phamacy Profile', image: '' }}
                                            onClick={() => {

                                            }}
                                        /> */}
                                        <hr className="my-2" />
                                        <ProfileOption
                                            icon={<><Svg.AndroidAppIcon /></>}
                                            name='Download Android App'
                                        />
                                        <ProfileOption
                                            icon={<><Svg.IosAppIcon /></>}
                                            name='Download IOS App'
                                        />
                                        <hr className="my-2" />
                                        <div className="flex gap-2 mt-3 transition-all items-center rounded-md cursor-pointer group px-[6px] py-[4px] hover:bg-slate-300">
                                            <svg width="20" height="19" viewBox="0 0 20 19" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                <path d="M4.54545 8.18182H11.8182V10H4.54545V12.7273L0 9.09091L4.54545 5.45455V8.18182ZM3.63636 14.5455H6.09818C7.1479 15.4712 8.44244 16.0744 9.82648 16.2827C11.2105 16.4909 12.6252 16.2954 13.9009 15.7196C15.1766 15.1437 16.259 14.212 17.0182 13.0362C17.7775 11.8604 18.1814 10.4905 18.1814 9.09091C18.1814 7.69129 17.7775 6.3214 17.0182 5.14563C16.259 3.96985 15.1766 3.03813 13.9009 2.46227C12.6252 1.88642 11.2105 1.69089 9.82648 1.89915C8.44244 2.10741 7.1479 2.71061 6.09818 3.63637H3.63636C4.48242 2.50653 5.5803 1.58956 6.84279 0.958313C8.10528 0.327069 9.49759 -0.00105786 10.9091 2.56208e-06C15.93 2.56208e-06 20 4.07 20 9.09091C20 14.1118 15.93 18.1818 10.9091 18.1818C9.49759 18.1829 8.10528 17.8548 6.84279 17.2235C5.5803 16.5923 4.48242 15.6753 3.63636 14.5455Z" className={`fill-[#0A1C4B]`} />
                                            </svg>
                                            <p className={`text-[#0A1C4B] font-[500] `}>Logout</p>
                                        </div>
                                    </>
                                    :
                                    <>
                                        <div className="flex items-center justify-center h-[80px]">
                                            <Svg.TcLoader />
                                        </div>
                                    </>
                            }
                        </div>
                    </>
                }
                <div
                    onClick={handleDropdownOpen}
                    className={`flex group ${sideBarActive ? 'gap-2 px-1' : 'gap-[3px]'} mt-3 items-center justify-between cursor-pointer hover:bg-gray-100 rounded-md py-[2px]`}>
                    <ProfileDisplay
                        data={bottom_active_profile ? bottom_active_profile?.data : {}}
                        sideBarActive={sideBarActive}
                        isBottomMain={true}
                    />
                    <span className="mr-2">
                        <Svg.Chevron color={'#374151'} className={`${is_dropdown_open ? 'mb-2' : 'group-hover:mb-2 mb-1'} transition-all ${!sideBarActive ? 'w-[10px]' : ''}`} />
                        <Svg.Chevron color={'#374151'} direction='BOTTOM' className={`${!sideBarActive ? 'w-[10px]' : ''}`} />
                    </span>
                </div>
            </div>
        </>
    )
}

export default SidebarBottomUser