

import URLs from "../URLs"
import Constants from "../../../Constants"

import ProfileTypes from "../../../Redux/ActionTypes/ProfileTypes";
import UserType from "../../../Redux/ActionTypes/UserType";
import Cookies from "js-cookie";


export const getMySidebarProfiles = (data, success, fail) => dispatch => {
    let s_code;
    let headers = {
        Authorization: `Token ${Cookies.get('auth_token')}`,
    }
    dispatch({
        type: ProfileTypes.GET_MY_PROFILES_API_CALLED,
        payload: true
    })
    fetch(`${URLs.BASE_URL_V1}${URLs.profile.get_my_sidebar_profiles_url}`,
        {
            headers: headers,
        })
        .then(response => {
            return response.json()
        })
        .then(result => {
            dispatch({
                type: ProfileTypes.GET_MY_SIDEBAR_PROFILES_DONE,
                payload: {
                    data: result.response.data
                }
            })
        })
        .catch(err => {
            dispatch({
                type: ProfileTypes.GET_MY_PROFILES_API_CALLED,
                payload: false
            })
        })
}

