export const CreateProfile = (data, success, fail) => dispatch => {
    let headers = {
        Authorization: `Token ${Cookies.get('auth_token')}`,
    }

    data = { ...data, ...data.media }
    delete data.media

    fetch(`${URLs.BASE_URL_V1}${URLs.doctor.create_doctor_profile_url}`,
        {
            headers: headers,
            method: 'POST',
            body: Constants.functions.getFormData(data)
        })
}

export const SwitchUserProfile = (data, success, fail) => dispatch => {
    let s_code;
    let headers = {
        Authorization: `Token ${Cookies.get('auth_token')}`,
        'Content-Type' : 'application/json'
    }

    fetch(`${URLs.BASE_URL_V1}${URLs.profile.switch_my_active_profile_url}`,
        {
            headers: headers,
            method : 'PUT',
            body : JSON.stringify({ profile_id : data.profile_id })
        })
        .then(response => {
            return response.json()
        })
        .then(result => {
            success && success()
        })
        .catch(err => {
            fail && fail()
        })
}

export const getMyRegisteredProfiles = (data, success, fail) => dispatch =>{
    let s_code;
    let headers = {
        Authorization: `Token ${Cookies.get('auth_token')}`,
    }

    fetch(`${URLs.BASE_URL_V1}${URLs.profile.get_my_already_registered_profiles_url}?user_id=${data.user_id}`,
        {
            headers: headers,
        })
        .then(response => {
            return response.json()
        })
        .then(result => {
            success && success()
            dispatch({
                type : ProfileTypes.GET_MY_REGISTERED_PROFILES,
                payload : {
                    types : result.response.businesses
                }
            })
        })
        .catch(err => {
            fail && fail()
        })
}