import specialityTypes from "../ActionTypes/specialityTypes"



const initialState = {
    specialities : {
        data : [],
        is_api_called : false,
        is_data_updated : false,
    }
}

const SpecialityReducer = (state = initialState, action) =>{

    let {type, payload} = action
    switch (type){
        case specialityTypes.GET_ALL_SPECIALITITES_API_CALLED:
            return {
                ...state,
                specialities : {
                    ...state.specialities,
                    is_api_called : true
                }
            }
        case specialityTypes.GET_ALL_SPECIALITITES_DONE:
            return {
                ...state,
                specialities : {
                    ...state.specialities,
                    data : payload.data,
                    is_data_updated : true
                }
            }
        default :
            return state
    }
}

export default SpecialityReducer