const TraumaCareCompleteLogo = ({ path, showText }) => {
    return (
        <Link to={path ? path : '#'}>
            <div className="flex cursor-pointer items-center">
                <TCLogoIcon />
                {
                    showText &&
                    <TcLogoText />
                }
            </div>
        </Link>
    )
}


