
import { useState, useEffect } from "react"
import Svg from "../../../Components/Svg"
import TcForm from "../../../Components/TcForm"
import GeneralDetailsForm from "./GeneralDetails"
import SelectionSection from "./SelectionSection"
import { connect } from 'react-redux'
import { getAllSpecialities } from "../../../Adapters/Apis/Speciality"
import { getAllDiseases } from "../../../Adapters/Apis/Disease"


let total_steps = 3

const GeneralDetails = ({ ProfileData, onUpdate, redux_speciality, redux_disease, ...props }) => {

      const [innerStep, setInnerStep] = useState(1)
      const [selectedSpecialities, setSelectedSpecialites] = useState([])
      const [selectedDiseases, setSelectedDiseases] = useState([])
      const [btn_loading, setBtnLoading] = useState(false)

      const handleSubmit = (e) => {
            setBtnLoading(true)
            if (innerStep == 1) {
                  setInnerStep(innerStep + 1)
            }
            else if (innerStep == 2) {
                  onUpdate({
                        specialities: selectedSpecialities,
                  })
                  setInnerStep(3)
            }
            else if (innerStep == 3) {
                  onUpdate({
                        diseases: selectedDiseases,
                        active_step: ProfileData.active_step + 1
                  })
                  setInnerStep(3)
            }
            setBtnLoading(false)
      }

      useEffect(()=>{
            if (ProfileData.specialities && ProfileData.specialities.length > 0 ){
                  setSelectedSpecialites(ProfileData.specialities)
            }

            if (ProfileData.diseases && ProfileData.diseases.length > 0 ){
                  setSelectedDiseases(ProfileData.diseases)
            }
      } , [])


      useEffect(() => {
            if (innerStep == 2) {
                  if (!redux_speciality.is_api_called) {
                        props.getAllSpecialities()
                  }

            }
            else if (innerStep == 3) {
                  if (!redux_disease.is_api_called) {
                        props.getAllDiseases()
                  }
            }
      }, [innerStep])

      return (
            <>
                  <div className="mt-[40px]">
                        <div className="border py-4 px-10 rounded-3xl relative max-w-[800px] mx-auto">
                              <span className="absolute top-2 right-4 text-[#24D0D0] font-[500]">{innerStep}/{total_steps}</span>
                              {
                                    innerStep == 1 &&
                                    <GeneralDetailsForm
                                          ProfileData={ProfileData}
                                          onUpdate={onUpdate}
                                          onSubmit={handleSubmit}
                                    />
                              }
                              {
                                    innerStep == 2 &&
                                    <SelectionSection
                                          heading='Select your Major Specialities'
                                          description='An official specialisation which will be recorded on an academic record or transcript, or reflected on official graduation documents.'
                                          ProfileData={ProfileData}
                                          onUpdate={onUpdate}
                                          selectedData={selectedSpecialities}
                                          setSelectedData={setSelectedSpecialites}
                                          content={redux_speciality}
                                          onGoBack={() => {
                                                setInnerStep(innerStep - 1)
                                          }}
                                    />
                              }
                              {
                                    innerStep == 3 &&
                                    <SelectionSection
                                          heading='Select your Major Diseases'
                                          description='An official specialisation which will be recorded on an academic record or transcript, or reflected on official graduation documents.'
                                          ProfileData={ProfileData}
                                          onUpdate={onUpdate}
                                          selectedData={selectedDiseases}
                                          setSelectedData={setSelectedDiseases}
                                          content={redux_disease}
                                          onGoBack={() => {
                                                setInnerStep(innerStep - 1)
                                          }}
                                    />
                              }
                              <TcForm.Button
                                    side='RIGHT'
                                    onClick={handleSubmit}
                                    loading={btn_loading}
                              >
                                    <span className={`flex items-center gap-2`}>
                                          <span className="text-white">Next</span>
                                          <span><Svg.LongArrow color={'#fff'} direction='RIGHT' /></span>
                                    </span>
                              </TcForm.Button>
                        </div>
                  </div>
            </>
      )
}

GeneralDetails.defaultProps = {
      ProfileData: {},
      onUpdate: () => { }
}


const mapStateToProps = (state) => {
      return {
            redux_speciality: state?.speciality?.specialities,
            redux_disease : state?.disease?.diseases,
      }
}

const mapDispatchToProps = {
      getAllSpecialities: getAllSpecialities,
      getAllDiseases: getAllDiseases
}

export default connect(mapStateToProps, mapDispatchToProps)(GeneralDetails)
