import { useEffect, useState } from "react"


const SwitchButton = ({ options, value, checked, onChange, name }) => {
    const [left_label, setLeftLabel] = useState(undefined)
    const [right_label, setRightLabel] = useState(undefined)

    const handleChange = () => {

        let next_val = checked ? left_label?.value : right_label?.value
        
        onChange(
            {
                target : {
                    name : name,
                    value : next_val,
                    checked : !checked
                }
            }
        )
    }

    useEffect(() => {
        setRightLabel(options?.find(opt => opt.active_side))
        setLeftLabel(options?.find(opt => !opt.active_side))

    }, [options])

    return (
        <>
            <div className={`flex items-center gap-2`}>
                {
                    left_label && left_label.show && 
                    <span>
                        {left_label.label}
                    </span>
                }
                <div className={`w-[45px] relative h-[26px] ${checked ? 'bg-[#24D0D0]' : 'bg-[#cfd2d8]'} rounded-[4px] flex justify-between items-center px-[3px] py-[3px]`}>
                    <span className="block h-[12px] w-[2px] bg-white rounded-md ml-[4px]"></span>
                    <div
                        className={`bg-white active:scale-90 h-[20px] w-[20px] absolute transition-all transform cursor-pointer border rounded-[4px] ${checked ? 'left-[22px] border-white' : 'left-[3px] border-[#878F96]'}`}
                        onClick={handleChange}
                    >
                    </div>
                    <span className="block h-[10px] w-[10px] rounded-full bg-white mr-[2px] border border-[#878F96]"></span>
                </div>
                {
                    right_label && right_label.show && 
                    <span className="text-gray-700">
                        {right_label.label}
                    </span>
                }
            </div>
        </>
    )
}


SwitchButton.defaultProps = {
    value: undefined,
    options: [],
    checked: true,
    onChange: () => { },
    name : undefined
}

export default SwitchButton