


import React, { useEffect, useState } from 'react'

import TcForm from '../../TcForm'
import Svg from '../../Svg'

import { TWENTY_MINUTES_DURATION } from '../../../Constants/Data/Time'
import { useSelector } from 'react-redux'




export const AddTimeSlotPopup = ({ allowTime, heading, onAddSlot, setPopupOpen, selected_day, slots, ...props }) => {

  const state = useSelector(state => state)

  const [our_service_fee, setServiceFee] = useState(20)

  const [start_time_options, setStartTimeOpts] = useState([])
  const [end_time_options, setEndTimeOpts] = useState([])
  const [data, setData] = useState({})
  const [total, setTotal] = useState(0)

  const handleChange = (e) => {
    let { name, value } = e.target
    setData({
      ...data,
      [name]: value,
    })
  }

  const handleSubmit = () => {

    
    let timeslot_data = {
      ...data,
      discount: data.discount || 0,
      our_service_fee: our_service_fee,
      total: total,
    }
    if (allowTime) {
      let start_time = TWENTY_MINUTES_DURATION.find(itm => itm.value == data.start_time)
      let end_time = TWENTY_MINUTES_DURATION.find(itm => itm.value == data.end_time)
      
      timeslot_data.start_label = start_time?.label
      timeslot_data.duration = end_time.minutes - start_time.minutes
      timeslot_data.end_label = end_time?.label
    }


    onAddSlot(timeslot_data)
    setData({
      doctor_fee: data.doctor_fee,
      discount: data.discount || 0,
    })
    setPopupOpen(false)
  }

  useEffect(() => {
    if (data.doctor_fee) {
      let fee = data.doctor_fee ? data.doctor_fee : 0
      fee = +fee

      let discount = +data.discount
      let doctor_total = fee
      if (discount) {
        doctor_total = ((100 - discount) * fee) / 100
      }

      let service_total = (our_service_fee * fee) / 100
      let gros_total = service_total + doctor_total
      gros_total = gros_total.toFixed(2)
      setTotal(gros_total)
    }
  }, [data.doctor_fee, data.discount])

  useEffect(() => {
    if (allowTime) {
      let time_frame = [...TWENTY_MINUTES_DURATION]

      slots?.forEach(slot => {
        let start_index = time_frame.findIndex(itm => itm.value == slot.start_time)
        start_index = start_index >= 0 ? start_index : 0
        let end_index = time_frame.findIndex(itm => itm.value == slot.end_time)
        let start_label = time_frame.find(itm => itm.value == slot.start_time)?.label
        let end_label = time_frame.find(itm => itm.value == slot.end_time)?.label

        // Here you need to handle In between difference 

        let diff = end_index - start_index
        time_frame.splice(start_index, diff + 1, { label: <>Reserved<br /><span className='text-[14px]'>{start_label} - {end_label}</span></>, value: 'No value', disabled: true, border: true })
      })
      setStartTimeOpts(time_frame)
      setEndTimeOpts(time_frame)
    }
  }, [data?.start_time, slots, allowTime])

  useEffect(() => {
    setServiceFee(state.traumacare.service_fee)
  }, [state.traumacare.service_fee])

  return (
    <>
      <TcForm.Popup
        heading={heading ? heading : `Add ${selected_day}s Availability Time Slot`}
        onClose={() => {
          setPopupOpen(false)
        }}
        widthClass={'max-w-[800px]'}
      >
        <div className='flex items-center gap-2 px-4 my-[30px]'>
          <div className='flex-1'>
            <Svg.TCLogoIcon className='mx-auto mb-3 w-[69px] h-[59px]' />
            <Svg.TcLogoText className='!mx-auto' />
            <span className='text-[#7E9CC3] block text-center mt-4'>Includes Traumacare Services Fee.</span>
          </div>
          <div className='flex-1'>
            {
              allowTime &&
              <>
                <div className='flex items-start gap-4'>
                  <TcForm.Dropdown
                    value={data.start_time}
                    name={'start_time'}
                    label={'Start Time'}
                    required={true}
                    placeholder={'Start Time'}
                    options={start_time_options}
                    onChange={handleChange}
                  />
                  <TcForm.Dropdown
                    value={data.end_time}
                    name={'end_time'}
                    label={'End Time'}
                    required={true}
                    placeholder={'End Time'}
                    options={end_time_options}
                    onChange={handleChange}
                  />
                </div>
              </>
            }
            <TcForm.InputField
              type={'number'}
              value={data.doctor_fee}
              name={'doctor_fee'}
              label={'Doctor will get'}
              required={true}
              placeholder={'Fee'}
              onChange={handleChange}
            />
            <TcForm.InputField
              icon={<><Svg.PercentageSymbol /></>}
              type={'number'}
              value={data.discount}
              name={'discount'}
              label={'Apply Discount'}
              placeholder={'Discount'}
              onChange={handleChange}
            />
            <TcForm.InputField
              icon={<><Svg.PercentageSymbol /></>}
              disabled
              type={'number'}
              value={our_service_fee}
              name={'service_fee'}
              label={'Our service Fee'}
              placeholder={'Service Fee'}
              onChange={handleChange}
            />
            <hr />
            <div className={`flex items-center justify-between gap-2 mt-3`}>
              <span className='text-xl font-medium italic'>Patient will pay</span>
              <span className='text-xl font-medium'>PKR {total ? total : 0}</span>
            </div>
            <TcForm.Button
              side={'RIGHT'}
              className={'mt-4'}
              onClick={handleSubmit}
            >
              Add Time Slot
            </TcForm.Button>
          </div>
        </div>
      </TcForm.Popup>
    </>
  )
}



AddTimeSlotPopup.defaultProps = {
  heading: '',
  allowTime: true,
  onAddSlot: () => { },
  setPopupOpen: () => { },
  selected_day: '',
  slots: [],
}


const TimeSlotBtn = ({ disabled, slots, onAddSlot, selected_day, onClick, ...props }) => {

  const [is_popup_open, setPopupOpen] = useState(false)

  return (
    <>
      {
        is_popup_open &&
        <AddTimeSlotPopup
          slots={slots}
          selected_day={selected_day}
          onAddSlot={onAddSlot}
          setPopupOpen={setPopupOpen}
        />
      }
      <div
        className={`${disabled ? 'bg-[#878F96] border-[#878F96] cursor-not-allowed' : 'border-[#24D0D0] bg-[#24D0D0] cursor-pointer'} border flex justify-center items-center px-2 py-2 rounded-lg`}
        onClick={(e) => {
          if (!disabled) {
            setPopupOpen(true)
          }
        }}
      >
        <p className="text-white text-[14px]">Add Time Slot</p>
      </div>
    </>
  )
}


TimeSlotBtn.defaultProps = {
  disabled: true,
  onClick: (e) => { },
  onAddSlot: (data) => { }
}


export default TimeSlotBtn