import { useEffect, useState } from "react"


const InputField = ({ showTitle, size, icon, iconBorder, onChange, disabled, type, value, name, label, onBlur, id, required, placeholder, error }) => {

    const [is_error, setIsError] = useState(false)

    const handleOnBlur = (event) => {
        if (required) {
            if (!value) {
                setIsError('This field is required')
            }
        }
        onBlur(event)
    }

    const handleChange = (event) => {
        setIsError(false)
        onChange(event)
    }

    useEffect(() => {
        setIsError(error)
    }, [error])

    return (
        <>
            <div className="mb-[10px]">
                {
                    showTitle &&
                    <label htmlFor={id ? id : name} className="text-[#6d8cba] mb-[6px] block text-[14px]">{label} {required ? '*' : ''}</label>
                }
                <div className={`${disabled ? 'bg-[#8989B610] cursor-not-allowed' : 'bg-[#23d0d010]'} flex items-stretch ${size == 'lg' ? 'gap-2' : 'gap-[2px]'}  border-[#23d0d010] rounded-[10px] border-[1.4px] ${is_error ? 'border-[#f25b52] bg-[#f25a5220]' : ''}`}>
                    {
                        icon &&
                        <span className={`flex items-center justify-center ${iconBorder ? 'border-r' : ''} ${size == 'lg' ? 'w-[50px]' : 'w-[30px]'}  ${is_error ? 'border-[#f25b52] bg-[#f25a5220]' : 'border-r-gray-100'}`}>
                            {icon}
                        </span>
                    }
                    <input
                        type={type}
                        value={value}
                        autocomplete="off"
                        name={name}
                        id={id ? id : name}
                        className={`block w-full flex-1 bg-transparent outline-none text-[15px] text-[#6c747e] ${size == 'lg' ? 'py-[16px] px-[20px]' : `px-[9px] ${icon ? '!pl-[2px]' : ''} py-[7px]`}  ${is_error ? 'text-[#d64139]' : ''} ${disabled ? 'cursor-not-allowed' : ''}`}
                        placeholder={placeholder}
                        onChange={handleChange}
                        onBlur={handleOnBlur}
                        disabled={disabled}
                    />
                </div>
                {is_error &&
                    <span className={`text-[#d64139] text-[12px] max-w-max ml-auto block`}>{is_error}</span>
                }
            </div>
        </>
    )
}

InputField.defaultProps = {
    showTitle: true,
    onChange: () => { },
    onBlur: () => { },
    type: 'text',
    label: 'Enter Label Props',
    id: undefined,
    required: false,
    placeholder: 'Please provide placeholder',
    name: 'asdfas',
    value: '',
    error: false,
    disabled: false,
    icon: undefined,
    iconBorder : true,
    size : 'lg'
}


export default InputField